<template>
  <div class="topPrice">
    <div class="text">总价：{{ allMoney }}</div>
    <el-input-number v-model="iptNum" :min="0" @change="handleChange" />
  </div>
</template>
<script>
export default {
  data() {
    return {
      iptNum: 1, //不要直接修改从父组件传过来的props数据，在data函数中重新定义一个变量，将props数据数据赋值在data变量上，后面修改data变量即可。
    };
  },
  props: {
    num: {
      type: Number,
      default: 1,
    },
    price: {
      type: String,
      default: "0.00",
    },
  },

  computed: {
    allMoney() {
      return (Number(this.price) * this.iptNum).toFixed(2);
    },
  },
  created() {
    console.log("父组件传来的num", this.num);
    if (this.num) {
      this.iptNum = this.num; //父组件传来的num
    }
  },
  methods: {
    handleChange(value) {
      console.log("数量", value);
      this.$emit("change-num", value);
    },
  },
};
</script>
<style lang="scss" scoped>
.topPrice {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 10px 0;
}

.text {
  margin: 10px;
}
</style>
