<template>
  <div @click="hello">
    <input type="checkbox" v-model="agreeStatus" />
    <label><slot></slot></label>
  </div>
</template>

<script>
export default {
  name: "CheckBox",
  data() {
    return {
      agreeStatus: false,
    };
  },

  mounted() {},

  methods: {
    hello() {
      this.agreeStatus = !this.agreeStatus;
    },
    agree() {
      console.log(this.agreeStatus);
    },
  },
};
</script>
