import Vue from "vue";
import VueRouter from "vue-router";
import Login from "../views/login/index.vue";
import Home from "../views/home/index.vue";
import Cart from "../views/cart/index.vue";
import Password from "../views/password/index.vue";
import Result from "../views/result/index.vue";
import MxGraph from "../views/test/mxgraph.vue";
import Data from "../views/test/data.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Helloworld",
    component: Login,
  },
  {
    path: "/home",
    name: "Home",
    component: Home,
  },
  {
    path: "/cart",
    name: "Cart",
    component: Cart,
  },
  {
    path: "/password",
    name: "Password",
    component: Password,
  },
  {
    path: "/result",
    name: "Result",
    component: Result,
  },
  {
    path: "/mxgraph",
    name: "MxGraph",
    component: MxGraph,
  },
  {
    path: "/data",
    name: "Data",
    component: Data,
    // redirect: "/data",
    // children: [
    //   {
    //     path: "img",
    //     name: "Img",
    //     component: () => import("@/views/test/ImgDom.vue"),
    //   },
    // ],
  },
];

const router = new VueRouter({
  routes,
});

export default router;
