<template>
  <div class="homePage">
    <Title msg="商品选购" a="123" :showCart="true"></Title>
    <div class="goodsList">
      <GoodsItem
        v-for="item in goodsList"
        :item="item"
        :key="item.id"
        @message="clickGoods(item)"
      >
      </GoodsItem>
    </div>
    <!-- 弹出商品详情 -->
    <el-drawer
      size="100%"
      direction="btt"
      :title="checkGoods.name"
      :destroy-on-close="true"
      :visible.sync="drawer"
    >
      <div style="padding: 10px">
        <img style="width: 100%" :src="checkGoods.picture" alt="" />
        <P class="desCss">商品编号：{{ checkGoods.id }}</P>
        <P class="desCss">商品描述：{{ checkGoods.desc }}</P>
        <P class="desCss price">单品价格：¥{{ checkGoods.price }}</P>
      </div>
      <footer style="padding: 10px">
        <InputNumber
          @change-num="changeNum"
          :price="checkGoods.price"
          :num="checkGoods.num"
        ></InputNumber>
        <div class="btns">
          <el-button type="primary" class="btn" @click="add"
            >加入购物车</el-button
          >
          <el-button type="success" class="btn" @click="pay"
            >立即支付</el-button
          >
        </div>
      </footer>
    </el-drawer>
  </div>
</template>

<script>
import { getGoods } from "@/utils/api";
import GoodsItem from "./components/GoodsInfo.vue";
import InputNumber from "@/components/InputNumber.vue";
export default {
  data() {
    return {
      goodsList: [],
      drawer: false,
      checkGoods: "",
      num: 0,
    };
  },
  components: {
    GoodsItem,
    InputNumber,
  },
  created() {
    this.getGoodsList();
  },
  methods: {
    getGoodsList() {
      getGoods().then((res) => {
        console.log("res", res.result[1].goods);
        this.goodsList = res.result[1].goods.map((item) =>
          Object.assign(item, { num: 1 })
        );
        // this.goodsList = res.result[1].goods
      });
    },
    clickGoods(item) {
      this.drawer = true;
      this.checkGoods = item;
      console.log("选中的商品", this.checkGoods);
    },
    pay() {
      this.$router.push({
        name: "Password",
        query: {
          // ...this.checkGoods,
          num: 1,
        },
      });
    },
    add() {
      let oldList = localStorage.getItem("list");
      console.log("oldList", oldList);
      if (oldList) {
        let arr = Array.from(JSON.parse(oldList)); //将类数组对象转换为真正数组
        arr.push(this.checkGoods);
        alert("已加入购物车");
        localStorage.setItem("list", JSON.stringify(arr));
      } else {
        localStorage.setItem("list", JSON.stringify([this.checkGoods]));
        alert("已加入购物车");
      }
    },
    changeNum(value) {
      this.checkGoods.num = value;
    },
  },
};
</script>

<style scoped lang="scss">
.goodsList {
  padding: 0px 10px 50px 10px;
  background-color: #eee;
}

.price {
  font-size: 20px;
  color: rgb(190 119 119);
}

.titleHidden {
  width: 70%;
  padding: 0 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: 700;
}

.desCss {
  font-size: 16px;
  background: #eee;
  border-radius: 10px;
  padding: 5px 10px;
  margin: 10px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

footer {
  width: calc(100% - 20px);
  padding: 20px 20px 20px 0;
  position: absolute;
  bottom: 0;

  .btns {
    margin-top: 10px;
  }

  .btn {
    width: 48%;
  }
}
</style>
<style lang="scss">
.el-drawer__header {
  margin-bottom: 0 !important;
  color: #160c15;
}
</style>
