<template>
    <div>
        <Title msg="订单结算"></Title>
        <!-- 密码输入框 -->
        <van-password-input :value="value" :mask="false" :focused="showKeyboard" @focus="showKeyboard = true"
            info="请输入密码" :error-info="errorInfo" />
        <!-- 数字键盘 -->
        <van-number-keyboard v-model="value" :show="showKeyboard" @blur="showKeyboard = false" :maxlength="6" />
    </div>
</template>

<script>
export default {

    data() {
        return {
            value: '',
            showKeyboard: true,
            errorInfo: ""
        };
    },
    watch: {
        value(value) {
            if (value.length === 6 && value !== '123456') {
                this.errorInfo = '密码错误';
            } else if (value === '123456') {
                this.$router.push({ path: '/result' })
            }
            else {
                this.errorInfo = '';
            }
        },
    },
    computed: {
    },

    created() {
    },
    mounted() {
    },
    methods: {
    }
}
</script>

<style lang="scss" scoped>
.van-password-input {
    margin-top: 25px
}
</style>