                {{ item.name }}
            </div>
            <p class="des">
                {{ item.desc }}</p>
            <div class="price">
                ￥{{ item.price }}</div>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        item: { type: Object },
    },
    methods: {
        message() {
            this.$emit("message")
        }
    }
}
</script>

