<template>
    <div class="cartPage">
        <Title msg="购物车"></Title>
        <div v-if="list.length > 0">
            <div class="buyGoodsList" v-for="item in list" :key="item.id">
                <GoodsItem :item="item"></GoodsItem>
                <InputNumber :price="item.price" :num="item.num" @change-num="changeNum($event, item)"></InputNumber>
            </div>
            <el-button type="success" class="btn" @click="pay">立即支付</el-button>
        </div>
        <div v-if="list.length == 0">
            <van-empty description="暂无更多" />
            <el-button type="success" class="btn" @click="goBuy">去加购</el-button>
        </div>
    </div>
</template>
<script>
import GoodsItem from '@/views/home/components/GoodsInfo.vue'
import InputNumber from '@/components/InputNumber.vue'
export default {
    data() {
        return {
            list: [],
        }
    },
    components: { GoodsItem, InputNumber },
    created() {
        this.list = Array.from(JSON.parse(localStorage.getItem('list')) ?? [])//??（空值合并操作符）当左侧值为 null 或 undefined 时，返回 ?? 符号右边的值
    },
    methods: {
        pay() {
            localStorage.setItem('list', JSON.stringify(this.list))//本地缓存购物车信息
            this.$router.push("/password")
        },
        goBuy() {
            this.$router.push("/home")
        },
        changeNum(value, item) {
            console.log(value, item);
            item.num = value//子组件发出的事件传来的value
        }
    }
}
</script>

<style scoped lang="scss">
.btn {
    width: 80%;
    margin: 20px 9%;
    height: 40px;
}
</style>