-->
<template>
  <div class="loginPage">
    <el-form
      ref="formDataRef"
      label-position="left"
      :model="formData"
      :rules="rules"
      label-width="40px"
      class="demo-ruleForm"
    >
      <h2 class="title">vue2演示项目</h2>
      <el-form-item label="账号" prop="name">
        <el-input v-model="formData.name" type="text" />
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input v-model="formData.password" type="password" show-password />
      </el-form-item>
      <el-form-item>
        <el-button
          class="loginBtn"
          type="primary"
          @click="submit()"
          :disabled="disabled"
          >登录</el-button
        >
        <el-button class="loginBtn" @click="reset">重置 </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      formData: {
        name: "",
        password: "",
      },
      rules: {
        name: [
          {
            required: true, //是否必须字段
            message: "请输入用户名", // 触发的提示信息
            trigger: "blur", // 触发时机: 当失去焦点时（光标不显示的时候），触发此提示
          },
          {
            min: 3, // 最小字符数
            max: 5, // 最大字符数
            message: "用户名长度需要在3-5个字符之间", // 触发的提示信息
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true, //是否必须字段
            message: "请输入密码", // 触发的提示信息
            trigger: "blur", // 触发时机: 当失去焦点时（光标不显示的时候），触发此提示
          },
          {
            min: 3, // 最小字符数
            max: 6, // 最大字符数
            message: "密码长度需要在3-5个字符之间", // 触发的提示信息
            trigger: "blur",
          },
        ],
      },
    };
  },
  computed: {
    disabled() {
      return this.formData.name === "" && this.formData.password === "";
    },
  },

  methods: {
    submit() {
      this.$refs["formDataRef"].validate((valid) => {
        if (valid) {
          this.$router.push("/home");
        }
      });
    },
    reset() {
      this.formData.name = "";
      this.formData.password = "";
    },
  },
};
</script>

<style scoped lang="scss">
.loginPage {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-image: url("../../assets/bj1.jpg");
  background-size: 100% 100%;

  .demo-ruleForm {
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: start;
    padding: 20px;

    .title {
      color: #fff;
      margin-bottom: 10px;
      text-align: center;
      width: 100%;
    }

    .el-form-item {
      width: 100%;
      color: #fff;

      .loginBtn {
        width: 47%;
      }
    }
  }
}

::v-deep.el-form--label-left .el-form-item__label {
  padding: 0px;
}
</style>
