// import {
//     resolve
// } from "core-js/es6/promise";
import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);


const store = new Vuex.Store({
    state: {
        // 定义一个name，以供全局使用
        name: '张三',
        // 定义一个number，以供全局使用
        number: 0,
        // 定义一个list，以供全局使用
        list: [{
                id: 1,
                name: '111'
            },
            {
                id: 2,
                name: '222'
            },
            {
                id: 3,
                name: '333'
            },
        ],
    },
    //在store对象中增加getter属性
    getters: {
        getMessage(state) {
            // 获取修饰后的name，第一个参数state为必要参数，必须写在形参上
            return `Hello ${state.name}`;
        }
    },
    //增加mutations属性
    mutations: {
        //没有传参
        setNumber(state) {
            state.number = 5;
        },
        //传不固定的值
        // setNumberIsWhat(state, number) {
        //     state.number = number;
        // },
        //官方建议传递一个对象进去，这样看起来更美观，对象的名字你可以随意命名，但我们一般命名为payload
        setNumberIsWhat(state, payload) {
            // 增加一个带参数的mutations方法，并且官方建议payload为一个对象
            state.number = payload.number;
        }
    },

    actions: {
        // 增加actions属性
        setNum(content, payload) {
            // 增加setNum方法，默认第一个参数是content，其值是复制的一份store
            return new Promise(resolve => {
                // 我们模拟一个异步操作，1秒后修改number为777
                setTimeout(() => {
                    // content.commit('setNumberIsWhat', {
                    //     number: 777
                    // });
                    content.commit('setNumberIsWhat', {
                        number: payload.number
                    })
                    resolve();
                }, 1000)
            })
        }
    },
    //或者
    // actions: {
    //     setNum({ commit }) {
    //       // 直接将content结构掉，解构出commit，下面就可以直接调用了
    //       return new Promise(resolve => {
    //         setTimeout(() => {
    //           commit('XXXX'); // 直接调用
    //           resolve();
    //         }, 1000);
    //       });
    //     },
});

export default store;